<template>
	<div>
		<el-dialog modal-class="edit_form_modal" class="edit_form" v-model="formVisible" :title="formTitle" width="80%" destroy-on-close :fullscreen='false'>
			<el-form class="formModel_form" ref="formRef" :model="form" :rules="rules">
				<el-row >
					<el-col :span="12">
						<el-form-item label="饮品名称" prop="yinpinmingcheng">
							<el-input class="list_inp" v-model="form.yinpinmingcheng" placeholder="饮品名称"
                                type="text"
								:readonly="!isAdd||disabledForm.yinpinmingcheng?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="12">
						<el-form-item prop="yinpintupian"
									  label="饮品图片"
						>
							<uploads
								:disabled="!isAdd||disabledForm.yinpintupian?true:false"
								action="file/upload"
								tip="请上传饮品图片"
								style="width: 100%;text-align: left;"
								:fileUrls="form.yinpintupian?form.yinpintupian:''" 
								@change="yinpintupianUploadSuccess">
							</uploads>
						</el-form-item>
					</el-col>
					<el-col :span="12">
						<el-form-item label="饮品分类" prop="yinpinfenlei">
							<el-select
								class="list_sel"
								:disabled="!isAdd||disabledForm.yinpinfenlei?true:false"
								v-model="form.yinpinfenlei" 
								placeholder="请选择饮品分类"
								>
								<el-option v-for="(item,index) in yinpinfenleiLists" :label="item"
									:value="item"
									>
								</el-option>
							</el-select>
						</el-form-item>
					</el-col>
					<el-col :span="12">
						<el-form-item label="店铺分类" prop="dianpufenlei">
							<el-select
								class="list_sel"
								:disabled="!isAdd||disabledForm.dianpufenlei?true:false"
								v-model="form.dianpufenlei" 
								placeholder="请选择店铺分类"
								>
								<el-option v-for="(item,index) in dianpufenleiLists" :label="item"
									:value="item"
									>
								</el-option>
							</el-select>
						</el-form-item>
					</el-col>
					<el-col :span="12">
						<el-form-item label="规格" prop="guige">
							<el-select
								class="list_sel"
								:disabled="!isAdd||disabledForm.guige?true:false"
								v-model="form.guige" 
								placeholder="请选择规格"
								>
								<el-option v-for="(item,index) in guigeLists" :label="item"
									:value="item"
									>
								</el-option>
							</el-select>
						</el-form-item>
					</el-col>
					<el-col :span="12">
						<el-form-item label="冷热饮" prop="lengreyin">
							<el-select
								class="list_sel"
								:disabled="!isAdd||disabledForm.lengreyin?true:false"
								v-model="form.lengreyin" 
								placeholder="请选择冷热饮"
								>
								<el-option v-for="(item,index) in lengreyinLists" :label="item"
									:value="item"
									>
								</el-option>
							</el-select>
						</el-form-item>
					</el-col>
					<el-col :span="12">
						<el-form-item label="甜度" prop="tiandu">
							<el-select
								class="list_sel"
								:disabled="!isAdd||disabledForm.tiandu?true:false"
								v-model="form.tiandu" 
								placeholder="请选择甜度"
								>
								<el-option v-for="(item,index) in tianduLists" :label="item"
									:value="item"
									>
								</el-option>
							</el-select>
						</el-form-item>
					</el-col>
					<el-col :span="12">
						<el-form-item label="店长账号" prop="dianzhangzhanghao">
							<el-input class="list_inp" v-model="form.dianzhangzhanghao" placeholder="店长账号"
                                type="text"
								:readonly="!isAdd||disabledForm.dianzhangzhanghao?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="12">
						<el-form-item label="店铺名称" prop="dianpumingcheng">
							<el-input class="list_inp" v-model="form.dianpumingcheng" placeholder="店铺名称"
                                type="text"
								:readonly="!isAdd||disabledForm.dianpumingcheng?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="12">
						<el-form-item label="店铺地址" prop="dianpudizhi">
							<el-input class="list_inp" v-model="form.dianpudizhi" placeholder="店铺地址"
                                type="text"
								:readonly="!isAdd||disabledForm.dianpudizhi?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="12">
						<el-form-item label="单限" prop="onelimittimes">
							<el-input class="list_inp" v-model.number="form.onelimittimes" placeholder="单限"
                                type="text"
								:readonly="!isAdd||disabledForm.onelimittimes?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="12">
						<el-form-item label="库存" prop="alllimittimes">
							<el-input class="list_inp" v-model.number="form.alllimittimes" placeholder="库存"
                                type="text"
								:readonly="!isAdd||disabledForm.alllimittimes?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="12">
						<el-form-item label="价格" prop="price">
							<el-input class="list_inp" v-model.number="form.price" placeholder="价格"
                                type="number"
								:readonly="!isAdd||disabledForm.price?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="12">
						<el-form-item label="负责员工" prop="yuangongzhanghao">
							<el-input class="list_inp" v-model="form.yuangongzhanghao" placeholder="负责员工"
                                type="text"
								:readonly="!isAdd||disabledForm.yuangongzhanghao?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="24">
						<el-form-item label="饮品详情" prop="yinpinxiangqing">
							<editor :value="form.yinpinxiangqing" placeholder="请输入饮品详情" :readonly="!isAdd||disabledForm.yinpinxiangqing?true:false"
								class="list_editor" @change="(e)=>editorChange(e,'yinpinxiangqing')"></editor>
						</el-form-item>
					</el-col>
				</el-row>
			</el-form>
			<template #footer v-if="isAdd||type=='logistics'||type=='reply'">
				<span class="formModel_btn_box">
					<el-button class="cancel_btn" @click="closeClick">取消</el-button>
					<el-button class="confirm_btn" type="primary" @click="save"
						>
						提交
					</el-button>
				</span>
			</template>
		</el-dialog>
	</div>
</template>
<script setup>
	import {
		reactive,
		ref,
		getCurrentInstance,
		nextTick,
		computed,
		defineEmits
	} from 'vue'
    import {
        useStore
    } from 'vuex';
    const store = useStore()
    const user = computed(()=>store.getters['user/session'])
	const context = getCurrentInstance()?.appContext.config.globalProperties;	
	const emit = defineEmits(['formModelChange'])
	//基础信息
	const tableName = 'yinpinxinxi'
	const formName = '饮品信息'
	//基础信息
	//form表单
	const form = ref({})
	const disabledForm = ref({
        yinpinmingcheng : false,
        yinpintupian : false,
        yinpinfenlei : false,
        dianpufenlei : false,
        guige : false,
        lengreyin : false,
        tiandu : false,
        yinpinxiangqing : false,
        storeupNumber : false,
        discussNumber : false,
        dianzhangzhanghao : false,
        dianpumingcheng : false,
        dianpudizhi : false,
        onelimittimes : false,
        alllimittimes : false,
        price : false,
        yuangongzhanghao : false,
	})
	const formVisible = ref(false)
	const isAdd = ref(false)
	const formTitle = ref('')
    
	const rules = ref({
		yinpinmingcheng: [
			{required: true,message: '请输入',trigger: 'blur'}, 
		],
		yinpintupian: [
		],
		yinpinfenlei: [
			{required: true,message: '请输入',trigger: 'blur'}, 
		],
		dianpufenlei: [
		],
		guige: [
			{required: true,message: '请输入',trigger: 'blur'}, 
		],
		lengreyin: [
			{required: true,message: '请输入',trigger: 'blur'}, 
		],
		tiandu: [
			{required: true,message: '请输入',trigger: 'blur'}, 
		],
		yinpinxiangqing: [
		],
		storeupNumber: [
			{ validator: context.$toolUtil.validator.intNumber, trigger: 'blur' },
		],
		discussNumber: [
			{ validator: context.$toolUtil.validator.intNumber, trigger: 'blur' },
		],
		dianzhangzhanghao: [
		],
		dianpumingcheng: [
		],
		dianpudizhi: [
		],
		onelimittimes: [
			{ validator: context.$toolUtil.validator.intNumber, trigger: 'blur' },
		],
		alllimittimes: [
			{ validator: context.$toolUtil.validator.intNumber, trigger: 'blur' },
		],
		price: [
			{ validator: context.$toolUtil.validator.number, trigger: 'blur' },
		],
		yuangongzhanghao: [
		],
	})
	//表单验证
	
	const formRef = ref(null)
	const id = ref(0)
	const type = ref('')
	//饮品图片上传回调
	const yinpintupianUploadSuccess=(e)=>{
		form.value.yinpintupian = e
	}
	//饮品分类列表
	const yinpinfenleiLists = ref([])
	//店铺分类列表
	const dianpufenleiLists = ref([])
	//规格列表
	const guigeLists = ref([])
	//冷热饮列表
	const lengreyinLists = ref([])
	//甜度列表
	const tianduLists = ref([])

	//获取唯一标识
	const getUUID =()=> {
      return new Date().getTime();
    }
	//重置
	const resetForm = () => {
		form.value = {
			yinpinmingcheng: '',
			yinpintupian: '',
			yinpinfenlei: '',
			dianpufenlei: '',
			guige: '',
			lengreyin: '',
			tiandu: '',
			yinpinxiangqing: '',
			storeupNumber: '',
			discussNumber: '',
			dianzhangzhanghao: '',
			dianpumingcheng: '',
			dianpudizhi: '',
			yuangongzhanghao: '',
		}
	}
	//获取info
	const getInfo = ()=>{
		context?.$http({
			url: `${tableName}/info/${id.value}`,
			method: 'get'
		}).then(res => {
			let reg=new RegExp('../../../file','g')
			res.data.data.yinpinxiangqing = res.data.data.yinpinxiangqing?(res.data.data.yinpinxiangqing.replace(reg,'../../../cl12321836/file')):'';
			form.value = res.data.data
			formVisible.value = true
		})
	}
	const crossRow = ref('')
	const crossTable = ref('')
	const crossTips = ref('')
	const crossColumnName = ref('')
	const crossColumnValue = ref('')
	//初始化
	const init=(formId=null,formType='add',formNames='',row=null,table=null,statusColumnName=null,tips=null,statusColumnValue=null)=>{
		resetForm()
		if(formId){
			id.value = formId
			type.value = formType
		}
		if(formType == 'add'){
			isAdd.value = true
			formTitle.value = '新增' + formName
			formVisible.value = true
		}else if(formType == 'info'){
			isAdd.value = false
			formTitle.value = '查看' + formName
			getInfo()
		}else if(formType == 'edit'){
			isAdd.value = true
			formTitle.value = '修改' + formName
			getInfo()
		}
		else if(formType == 'cross'){
			isAdd.value = true
			formTitle.value = formNames
			// getInfo()
			for(let x in row){
				if(x=='yinpinmingcheng'){
					form.value.yinpinmingcheng = row[x];
					disabledForm.value.yinpinmingcheng = true;
					continue;
				}
				if(x=='yinpintupian'){
					form.value.yinpintupian = row[x];
					disabledForm.value.yinpintupian = true;
					continue;
				}
				if(x=='yinpinfenlei'){
					form.value.yinpinfenlei = row[x];
					disabledForm.value.yinpinfenlei = true;
					continue;
				}
				if(x=='dianpufenlei'){
					form.value.dianpufenlei = row[x];
					disabledForm.value.dianpufenlei = true;
					continue;
				}
				if(x=='guige'){
					form.value.guige = row[x];
					disabledForm.value.guige = true;
					continue;
				}
				if(x=='lengreyin'){
					form.value.lengreyin = row[x];
					disabledForm.value.lengreyin = true;
					continue;
				}
				if(x=='tiandu'){
					form.value.tiandu = row[x];
					disabledForm.value.tiandu = true;
					continue;
				}
				if(x=='yinpinxiangqing'){
					form.value.yinpinxiangqing = row[x];
					disabledForm.value.yinpinxiangqing = true;
					continue;
				}
				if(x=='storeupNumber'){
					form.value.storeupNumber = row[x];
					disabledForm.value.storeupNumber = true;
					continue;
				}
				if(x=='discussNumber'){
					form.value.discussNumber = row[x];
					disabledForm.value.discussNumber = true;
					continue;
				}
				if(x=='dianzhangzhanghao'){
					form.value.dianzhangzhanghao = row[x];
					disabledForm.value.dianzhangzhanghao = true;
					continue;
				}
				if(x=='dianpumingcheng'){
					form.value.dianpumingcheng = row[x];
					disabledForm.value.dianpumingcheng = true;
					continue;
				}
				if(x=='dianpudizhi'){
					form.value.dianpudizhi = row[x];
					disabledForm.value.dianpudizhi = true;
					continue;
				}
				if(x=='onelimittimes'){
					form.value.onelimittimes = row[x];
					disabledForm.value.onelimittimes = true;
					continue;
				}
				if(x=='alllimittimes'){
					form.value.alllimittimes = row[x];
					disabledForm.value.alllimittimes = true;
					continue;
				}
				if(x=='price'){
					form.value.price = row[x];
					disabledForm.value.price = true;
					continue;
				}
				if(x=='yuangongzhanghao'){
					form.value.yuangongzhanghao = row[x];
					disabledForm.value.yuangongzhanghao = true;
					continue;
				}
			}
			if(row){
				crossRow.value = row
			}
			if(table){
				crossTable.value = table
			}
			if(tips){
				crossTips.value = tips
			}
			if(statusColumnName){
				crossColumnName.value = statusColumnName
			}
			if(statusColumnValue){
				crossColumnValue.value = statusColumnValue
			}
			formVisible.value = true
		}

		context?.$http({
			url: `${context?.$toolUtil.storageGet('sessionTable')}/session`,
			method: 'get'
		}).then(res => {
			var json = res.data.data
			if(json.hasOwnProperty('dianzhangzhanghao')&& context?.$toolUtil.storageGet("role")!="管理员"){
				form.value.dianzhangzhanghao = json.dianzhangzhanghao
				disabledForm.value.dianzhangzhanghao = true;
			}
			if(json.hasOwnProperty('dianpumingcheng')&& context?.$toolUtil.storageGet("role")!="管理员"){
				form.value.dianpumingcheng = json.dianpumingcheng
				disabledForm.value.dianpumingcheng = true;
			}
			if(json.hasOwnProperty('dianpudizhi')&& context?.$toolUtil.storageGet("role")!="管理员"){
				form.value.dianpudizhi = json.dianpudizhi
				disabledForm.value.dianpudizhi = true;
			}
		})
		context?.$http({
			url: `option/yinpinfenlei/yinpinfenlei`,
			method: 'get'
		}).then(res=>{
			yinpinfenleiLists.value = res.data.data
		})
		context?.$http({
			url: `option/dianpufenlei/dianpufenlei`,
			method: 'get'
		}).then(res=>{
			dianpufenleiLists.value = res.data.data
		})
		guigeLists.value = "大杯,中杯,小杯".split(',')
		lengreyinLists.value = "热饮,正常冰,多冰,少冰,去冰".split(',')
		tianduLists.value = "七分甜(正常糖),五分甜,三分甜".split(',')
	}
	//初始化
	//声明父级调用
	defineExpose({
		init
	})
	//关闭
	const closeClick = () => {
		formVisible.value = false
	}
	//富文本
	const editorChange = (e,name) =>{
		form.value[name] = e
	}
	//提交
	const save= async ()=>{
		if(form.value.yinpintupian!=null) {
			form.value.yinpintupian = form.value.yinpintupian.replace(new RegExp(context?.$config.url,"g"),"");
		}
		var table = crossTable.value
		var objcross = JSON.parse(JSON.stringify(crossRow.value))
		let crossUserId = ''
		let crossRefId = ''
		let crossOptNum = ''
		if(type.value == 'cross'){
			if(crossColumnName.value!=''){
				if(!crossColumnName.value.startsWith('[')){
					for(let o in objcross){
						if(o == crossColumnName.value){
							objcross[o] = crossColumnValue.value
						}
					}
					//修改跨表数据
					changeCrossData(objcross)
				}else{
					crossUserId = user.value.id
					crossRefId = objcross['id']
					crossOptNum = crossColumnName.value.replace(/\[/,"").replace(/\]/,"")
				}
			}
		}
		formRef.value.validate((valid)=>{
			if(valid){
				if(crossUserId&&crossRefId){
					form.value.crossuserid = crossUserId
					form.value.crossrefid = crossRefId
					let params = {
						page: 1,
						limit: 1000, 
						crossuserid:form.value.crossuserid,
						crossrefid:form.value.crossrefid,
					}
					context?.$http({
						url: `${tableName}/page`,
						method: 'get', 
						params: params 
					}).then(res=>{
						if(res.data.data.total>=crossOptNum){
							context?.$toolUtil.message(`${crossTips.value}`,'error')
							return false
						}else{
							context?.$http({
								url: `${tableName}/${!form.value.id ? "save" : "update"}`,
								method: 'post', 
								data: form.value 
							}).then(async res=>{
								emit('formModelChange')
								context?.$toolUtil.message(`操作成功`,'success')
                                formVisible.value = false
							})
						}
					})
				}else{
					context?.$http({
						url: `${tableName}/${!form.value.id ? "save" : "update"}`,
						method: 'post', 
						data: form.value 
					}).then(async (res)=>{
						emit('formModelChange')
						context?.$toolUtil.message(`操作成功`,'success')
                        formVisible.value = false
					})
				}
			}else{
                context.$message.error('请完善信息')
            }
		})
	}
	//修改跨表数据
	const changeCrossData = async (row)=>{
        if(type.value == 'cross'){
            await context?.$http({
                url: `${crossTable.value}/update`,
                method: 'post',
                data: row
            }).then(res=>{})
        }
	}
</script>
<style lang="scss" scoped>
	// 表单
	.formModel_form{
		// form item
		:deep(.el-form-item) {
			//label
			.el-form-item__label {
			}
			// 内容盒子
			.el-form-item__content {
				// 输入框
				.list_inp {
				}
				// 下拉框
				.list_sel {
					//去掉默认样式
					.select-trigger{
						height: 100%;
						.el-input{
							height: 100%;
						}
					}
				}
				// 富文本
				.list_editor {
				}
				//图片上传样式
				.el-upload-list  {
					//提示语
					.el-upload__tip {
					}
					//外部盒子
					.el-upload--picture-card {
						//图标
						.el-icon{
						}
					}
					.el-upload-list__item {
					}
				}
			}
		}
	}
	// 按钮盒子
	.formModel_btn_box {
		.cancel_btn {
		}
		.cancel_btn:hover {
		}
		
		.confirm_btn {
		}
		.confirm_btn:hover {
		}
	}
</style>
