	import {
		createRouter,
		createWebHashHistory
	} from 'vue-router'
	import news from '@/views/news/list'
	import yuangong from '@/views/yuangong/list'
	import caigoujihua from '@/views/caigoujihua/list'
	import address from '@/views/address/list'
	import yinpinxinxi from '@/views/yinpinxinxi/list'
	import yinpinfenlei from '@/views/yinpinfenlei/list'
	import dianzhang from '@/views/dianzhang/list'
	import syslog from '@/views/syslog/list'
    import menu_manage from '@/views/menu_manage/list'
	import storeup from '@/views/storeup/list'
	import users from '@/views/users/list'
	import forum from '@/views/forum/list'
	import discussyinpinxinxi from '@/views/discussyinpinxinxi/list'
	import yonghu from '@/views/yonghu/list'
	import chat from '@/views/chat/list'
	import messages from '@/views/messages/list'
	import orders from '@/views/orders/list'
	import config from '@/views/config/list'
	import usersCenter from '@/views/users/center'
	import dianzhangRegister from '@/views/dianzhang/register'
	import dianzhangCenter from '@/views/dianzhang/center'
	import yuangongRegister from '@/views/yuangong/register'
	import yuangongCenter from '@/views/yuangong/center'

export const routes = [{
		path: '/login',
		name: 'login',
		component: () => import('../views/login.vue')
	},{
		path: '/',
		name: '首页',
		component: () => import('../views/index'),
		children: [{
			path: '/',
			name: '首页',
			component: () => import('../views/HomeView.vue'),
			meta: {
				affix: true
			}
		}, {
			path: '/updatepassword',
			name: '修改密码',
			component: () => import('../views/updatepassword.vue')
		}
		
		,{
			path: '/usersCenter',
			name: '管理员个人中心',
			component: usersCenter
		}
		,{
			path: '/dianzhangCenter',
			name: '店长个人中心',
			component: dianzhangCenter
		}
		,{
			path: '/yuangongCenter',
			name: '员工个人中心',
			component: yuangongCenter
		}
		,{
			path: '/news',
			name: '公告',
			component: news
		}
		,{
			path: '/yuangong',
			name: '员工',
			component: yuangong
		}
		,{
			path: '/caigoujihua',
			name: '采购计划',
			component: caigoujihua
		}
		,{
			path: '/address',
			name: '地址',
			component: address
		}
		,{
			path: '/yinpinxinxi',
			name: '饮品信息',
			component: yinpinxinxi
		}
		,{
			path: '/yinpinfenlei',
			name: '饮品分类',
			component: yinpinfenlei
		}
		,{
			path: '/dianzhang',
			name: '店长',
			component: dianzhang
		}
		,{
			path: '/syslog',
			name: '操作日志',
			component: syslog
		}
        ,{
            path: '/menu',
            name: '菜单权限管理',
            component: menu_manage
        }
		,{
			path: '/storeup',
			name: '我的收藏',
			component: storeup
		}
		,{
			path: '/users',
			name: '管理员',
			component: users
		}
		,{
			path: '/forum',
			name: '我的发布',
			component: forum
		}
		,{
			path: '/discussyinpinxinxi',
			name: '饮品信息评论-评论',
			component: discussyinpinxinxi
		}
		,{
			path: '/yonghu',
			name: '用户',
			component: yonghu
		}
		,{
			path: '/chat',
			name: '在线客服',
			component: chat
		}
		,{
			path: '/messages',
			name: '留言反馈',
			component: messages
		}
		,{
			path: '/orders',
			name: '订单管理',
			component: orders
		}
		,{
			path: '/config',
			name: '轮播图',
			component: config
		}
		]
	},
	{
		path: '/dianzhangRegister',
		name: '店长注册',
		component: dianzhangRegister
	},
	{
		path: '/yuangongRegister',
		name: '员工注册',
		component: yuangongRegister
	},
]

const router = createRouter({
	history: createWebHashHistory(process.env.BASE_URL),
	routes
})

export default router
