<template>
	<div>
		<el-dialog modal-class="edit_form_modal" class="edit_form" v-model="formVisible" :title="formTitle" width="80%" destroy-on-close :fullscreen='false'>
			<el-form class="formModel_form" ref="formRef" :model="form" :rules="rules">
				<el-row >
					<el-col :span="12">
						<el-form-item label="饮品名称" prop="yinpinmingcheng">
							<el-input class="list_inp" v-model="form.yinpinmingcheng" placeholder="饮品名称"
                                type="text"
								:readonly="!isAdd||disabledForm.yinpinmingcheng?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="12">
						<el-form-item label="饮品类型" prop="yinpinleixing">
							<el-input class="list_inp" v-model="form.yinpinleixing" placeholder="饮品类型"
                                type="text"
								:readonly="!isAdd||disabledForm.yinpinleixing?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="12">
						<el-form-item label="供应商" prop="gongyingshang">
							<el-input class="list_inp" v-model="form.gongyingshang" placeholder="供应商"
                                type="text"
								:readonly="!isAdd||disabledForm.gongyingshang?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="12">
						<el-form-item label="采购价格" prop="caigoujiage">
							<el-input class="list_inp" v-model.number="form.caigoujiage" placeholder="采购价格"
                                type="number"
								:readonly="!isAdd||disabledForm.caigoujiage?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="12">
						<el-form-item label="采购数量" prop="caigoushuliang">
							<el-input class="list_inp" v-model.number="form.caigoushuliang" placeholder="采购数量"
                                type="text"
								:readonly="!isAdd||disabledForm.caigoushuliang?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="12">
						<el-form-item label="计划时间" prop="jihuashijian">
							<el-date-picker
								class="list_date"
								v-model="form.jihuashijian"
								format="YYYY-MM-DD HH:mm:ss"
								value-format="YYYY-MM-DD HH:mm:ss"
								type="datetime"
								:readonly="!isAdd||disabledForm.jihuashijian?true:false"
								placeholder="请选择计划时间" />
						</el-form-item>
					</el-col>
					<el-col :span="12">
						<el-form-item label="店长账号" prop="dianzhangzhanghao">
							<el-input class="list_inp" v-model="form.dianzhangzhanghao" placeholder="店长账号"
                                type="text"
								:readonly="!isAdd||disabledForm.dianzhangzhanghao?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="12">
						<el-form-item label="店铺名称" prop="dianpumingcheng">
							<el-input class="list_inp" v-model="form.dianpumingcheng" placeholder="店铺名称"
                                type="text"
								:readonly="!isAdd||disabledForm.dianpumingcheng?true:false" />
						</el-form-item>
					</el-col>

				</el-row>
			</el-form>
			<template #footer v-if="isAdd||type=='logistics'||type=='reply'">
				<span class="formModel_btn_box">
					<el-button class="cancel_btn" @click="closeClick">取消</el-button>
					<el-button class="confirm_btn" type="primary" @click="save"
						>
						提交
					</el-button>
				</span>
			</template>
		</el-dialog>
	</div>
</template>
<script setup>
	import {
		reactive,
		ref,
		getCurrentInstance,
		nextTick,
		computed,
		defineEmits
	} from 'vue'
    import {
        useStore
    } from 'vuex';
    const store = useStore()
    const user = computed(()=>store.getters['user/session'])
	const context = getCurrentInstance()?.appContext.config.globalProperties;	
	const emit = defineEmits(['formModelChange'])
	//基础信息
	const tableName = 'caigoujihua'
	const formName = '采购计划'
	//基础信息
	//form表单
	const form = ref({})
	const disabledForm = ref({
        yinpinmingcheng : false,
        yinpinleixing : false,
        gongyingshang : false,
        caigoujiage : false,
        caigoushuliang : false,
        jihuashijian : false,
        dianzhangzhanghao : false,
        dianpumingcheng : false,
	})
	const formVisible = ref(false)
	const isAdd = ref(false)
	const formTitle = ref('')
    
	const rules = ref({
		yinpinmingcheng: [
			{required: true,message: '请输入',trigger: 'blur'}, 
		],
		yinpinleixing: [
		],
		gongyingshang: [
		],
		caigoujiage: [
			{ validator: context.$toolUtil.validator.number, trigger: 'blur' },
		],
		caigoushuliang: [
			{ validator: context.$toolUtil.validator.intNumber, trigger: 'blur' },
		],
		jihuashijian: [
		],
		dianzhangzhanghao: [
		],
		dianpumingcheng: [
		],
	})
	//表单验证
	
	const formRef = ref(null)
	const id = ref(0)
	const type = ref('')

	//获取唯一标识
	const getUUID =()=> {
      return new Date().getTime();
    }
	//重置
	const resetForm = () => {
		form.value = {
			yinpinmingcheng: '',
			yinpinleixing: '',
			gongyingshang: '',
			caigoujiage: '',
			caigoushuliang: '',
			jihuashijian: '',
			dianzhangzhanghao: '',
			dianpumingcheng: '',
		}
	}
	//获取info
	const getInfo = ()=>{
		context?.$http({
			url: `${tableName}/info/${id.value}`,
			method: 'get'
		}).then(res => {
			let reg=new RegExp('../../../file','g')
			form.value = res.data.data
			formVisible.value = true
		})
	}
	const crossRow = ref('')
	const crossTable = ref('')
	const crossTips = ref('')
	const crossColumnName = ref('')
	const crossColumnValue = ref('')
	//初始化
	const init=(formId=null,formType='add',formNames='',row=null,table=null,statusColumnName=null,tips=null,statusColumnValue=null)=>{
		resetForm()
		if(formId){
			id.value = formId
			type.value = formType
		}
		if(formType == 'add'){
			isAdd.value = true
			formTitle.value = '新增' + formName
			formVisible.value = true
		}else if(formType == 'info'){
			isAdd.value = false
			formTitle.value = '查看' + formName
			getInfo()
		}else if(formType == 'edit'){
			isAdd.value = true
			formTitle.value = '修改' + formName
			getInfo()
		}
		else if(formType == 'cross'){
			isAdd.value = true
			formTitle.value = formNames
			// getInfo()
			for(let x in row){
				if(x=='yinpinmingcheng'){
					form.value.yinpinmingcheng = row[x];
					disabledForm.value.yinpinmingcheng = true;
					continue;
				}
				if(x=='yinpinleixing'){
					form.value.yinpinleixing = row[x];
					disabledForm.value.yinpinleixing = true;
					continue;
				}
				if(x=='gongyingshang'){
					form.value.gongyingshang = row[x];
					disabledForm.value.gongyingshang = true;
					continue;
				}
				if(x=='caigoujiage'){
					form.value.caigoujiage = row[x];
					disabledForm.value.caigoujiage = true;
					continue;
				}
				if(x=='caigoushuliang'){
					form.value.caigoushuliang = row[x];
					disabledForm.value.caigoushuliang = true;
					continue;
				}
				if(x=='jihuashijian'){
					form.value.jihuashijian = row[x];
					disabledForm.value.jihuashijian = true;
					continue;
				}
				if(x=='dianzhangzhanghao'){
					form.value.dianzhangzhanghao = row[x];
					disabledForm.value.dianzhangzhanghao = true;
					continue;
				}
				if(x=='dianpumingcheng'){
					form.value.dianpumingcheng = row[x];
					disabledForm.value.dianpumingcheng = true;
					continue;
				}
			}
			if(row){
				crossRow.value = row
			}
			if(table){
				crossTable.value = table
			}
			if(tips){
				crossTips.value = tips
			}
			if(statusColumnName){
				crossColumnName.value = statusColumnName
			}
			if(statusColumnValue){
				crossColumnValue.value = statusColumnValue
			}
			formVisible.value = true
		}

		context?.$http({
			url: `${context?.$toolUtil.storageGet('sessionTable')}/session`,
			method: 'get'
		}).then(res => {
			var json = res.data.data
			if(json.hasOwnProperty('dianzhangzhanghao')&& context?.$toolUtil.storageGet("role")!="管理员"){
				form.value.dianzhangzhanghao = json.dianzhangzhanghao
				disabledForm.value.dianzhangzhanghao = true;
			}
			if(json.hasOwnProperty('dianpumingcheng')&& context?.$toolUtil.storageGet("role")!="管理员"){
				form.value.dianpumingcheng = json.dianpumingcheng
				disabledForm.value.dianpumingcheng = true;
			}
		})
	}
	//初始化
	//声明父级调用
	defineExpose({
		init
	})
	//关闭
	const closeClick = () => {
		formVisible.value = false
	}
	//富文本
	const editorChange = (e,name) =>{
		form.value[name] = e
	}
	//提交
	const save= async ()=>{
		var table = crossTable.value
		var objcross = JSON.parse(JSON.stringify(crossRow.value))
		let crossUserId = ''
		let crossRefId = ''
		let crossOptNum = ''
		if(type.value == 'cross'){
			if(crossColumnName.value!=''){
				if(!crossColumnName.value.startsWith('[')){
					for(let o in objcross){
						if(o == crossColumnName.value){
							objcross[o] = crossColumnValue.value
						}
					}
					//修改跨表数据
					changeCrossData(objcross)
				}else{
					crossUserId = user.value.id
					crossRefId = objcross['id']
					crossOptNum = crossColumnName.value.replace(/\[/,"").replace(/\]/,"")
				}
			}
		}
		formRef.value.validate((valid)=>{
			if(valid){
				if(crossUserId&&crossRefId){
					form.value.crossuserid = crossUserId
					form.value.crossrefid = crossRefId
					let params = {
						page: 1,
						limit: 1000, 
						crossuserid:form.value.crossuserid,
						crossrefid:form.value.crossrefid,
					}
					context?.$http({
						url: `${tableName}/page`,
						method: 'get', 
						params: params 
					}).then(res=>{
						if(res.data.data.total>=crossOptNum){
							context?.$toolUtil.message(`${crossTips.value}`,'error')
							return false
						}else{
							context?.$http({
								url: `${tableName}/${!form.value.id ? "save" : "update"}`,
								method: 'post', 
								data: form.value 
							}).then(async res=>{
								emit('formModelChange')
								context?.$toolUtil.message(`操作成功`,'success')
                                formVisible.value = false
							})
						}
					})
				}else{
					context?.$http({
						url: `${tableName}/${!form.value.id ? "save" : "update"}`,
						method: 'post', 
						data: form.value 
					}).then(async (res)=>{
						emit('formModelChange')
						context?.$toolUtil.message(`操作成功`,'success')
                        formVisible.value = false
					})
				}
			}else{
                context.$message.error('请完善信息')
            }
		})
	}
	//修改跨表数据
	const changeCrossData = async (row)=>{
        if(type.value == 'cross'){
            await context?.$http({
                url: `${crossTable.value}/update`,
                method: 'post',
                data: row
            }).then(res=>{})
        }
	}
</script>
<style lang="scss" scoped>
	// 表单
	.formModel_form{
		// form item
		:deep(.el-form-item) {
			//label
			.el-form-item__label {
			}
			// 内容盒子
			.el-form-item__content {
				// 输入框
				.list_inp {
				}
				//日期选择器
				.list_date {
				}
			}
		}
	}
	// 按钮盒子
	.formModel_btn_box {
		.cancel_btn {
		}
		.cancel_btn:hover {
		}
		
		.confirm_btn {
		}
		.confirm_btn:hover {
		}
	}
</style>
